<template>
	<view class="page bgwhite">
		<view v-if="!isGet" v-for="item in detailData" :key="item.id">
			<view class="list" :prop="detailData">
				<view class="list-item ch-flex">
					<view class="label">{{ $t('顺序号') }}：</view>
					<view class="ch-flex-1 value"> {{ item.sort }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('派工单号') }}：</view>
					<view class="ch-flex-1 value"> {{ item.dispatchOrderNo }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('机器编号') }}：</view>
					<view class="ch-flex-1 value"> {{ item.machineNos }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('模具编号') }}：</view>
					<view class="ch-flex-1 value"> {{ item.mouldNo }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('产品编号') }}：</view>
					<view class="ch-flex-1 value"> {{ item.itemNo }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('产品描述') }}：</view>
					<view class="ch-flex-1 value"> {{ item.itemName }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('派工数') }}：</view>
					<view class="ch-flex-1 value"> {{ item.dispatchQty }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('生产数') }}：</view>
					<view class="ch-flex-1 value"> {{ item.prodQty ? item.prodQty : "0" }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('欠生产数') }}：</view>
					<view class="ch-flex-1 value"> {{ item.remainNumber ? item.remainNumber : "0" }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('次品数') }}：</view>
					<view class="ch-flex-1 value"> {{ item.badQty ? item.badQty : "0" }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('良品数') }}：</view>
					<view class="ch-flex-1 value"> {{ item.goodQty ? item.goodQty : "0" }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('完成率') }}：</view>
					<view class="ch-flex-1 value">
						<view style="width: 300upx;height: auto;display: flex;flex-direction: row;">
							<u-line-progress :percentage="item.finishRate * 100" height="auto">
								<view class="u-percentage-slot" activeColor="#21C9A0"></view>
							</u-line-progress>
							<text class="u-percentage-slot" style="padding-left: 10upx"> {{ item.finishRate }}%</text>
						</view>
					</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('超产率') }}：</view>
					<view class="ch-flex-1 value">
						<view style="width: 300upx;height: auto;display: flex;flex-direction: row;">
							<u-line-progress :percentage="(item.overProdRate * 100).toFixed('2')" height="auto">
								<view class="u-percentage-slot" activeColor="#21C9A0"></view>
							</u-line-progress>
							<text class="u-percentage-slot" style="padding-left: 10upx"> {{ item.overProdRate }}%</text>
						</view>
					</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('计划开始时间') }}：</view>
					<view class="ch-flex-1 value"> {{ item.planStartTime }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('计划结束时间') }}：</view>
					<view class="ch-flex-1 value"> {{ item.planFinishTime }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('实际开始时间') }}：</view>
					<view class="ch-flex-1 value"> {{ item.startTime }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('实际结束时间') }}：</view>
					<view class="ch-flex-1 value"> {{ item.finishTime }}</view>
				</view>
				<view class="list-item ch-flex">
					<view class="label">{{ $t('派工状态') }}：</view>
					<view class="ch-flex-1 value"> {{ item.statusText }}</view>
				</view>
				<view class="list-item ch-flex">

				</view>
			</view>

		</view>
		<template v-else>
			<u-loading-icon text="loading"></u-loading-icon>
		</template>
	</view>
</template>

<script>
import orderApi from "@/api/order/index.js"
export default {
	bdicts: [{
		tableName: 'mes_dispatch_order',
		fieldName: 'dispatch_status'
	},],
	data() {
		return {
			isGet: true,
			detailData: null
		};
	},
	onLoad(options) {
		uni.$u.http.get(orderApi.detail + 'mo=' + options.id).then(result => {
			console.log('result', result);
			if (result.code === 200) {
				this.detailData = result.data
			}
		}).catch(er => {
			console.log('er', er);
		}).finally(() => {
			this.isGet = false
		})
	},
	onShow() {
		uni.setNavigationBarTitle({ // 修改头部标题
			title: this.$t("order.detail")
		});
	},
	computed: {
		//次品数
		rejectNum() {
			if (this.detailData) {
				const num = this.detailData.prodQty - this.detailData.goodQty
				return num > 0 ? num : 0
			} else {
				return 0
			}
		},
		//完成率
		completionRate() {
			if (this.detailData) {
				let prodQty = this.detailData.prodQty || 0
				let dispatchQty = this.detailData.dispatchQty || 0

				if (dispatchQty != 0) {
					const rate = (prodQty / dispatchQty) * 100
					return `${rate.toFixed(2)}`
				} else {
					return 0
				}
			} else {
				return 0
			}
		},
		//超产率
		overproductionRate() {
			return this.completionRate > 100 ? (this.completionRate - 100) : 0
		},
		//派工状态
		dispatchStatus() {
			let status = ""
			switch (this.detailData.dispatchStatus) {
				case 0:
					status = "未派工"
					break
				case 1:
					status = "已派工"
					break
				case 2:
					status = "已暂停"
					break
				case 3:
					status = "已插单"
					break
				case 4:
					status = "已结案"
					break

				default:
					break
			}
			return status
		}
	}
}
</script>

<style lang="scss" scoped>
.list {
	&-item {
		padding: 20upx 24upx;
		@include after();
		font-size: 28upx;
		border: 0;

		.label {
			width: auto;
			color: #333;
			font-weight: 500;
		}

		.value {
			color: #666;
		}
	}
}
</style>
